.Card {
    margin: 0.2%;
    width: 24.6%;
    height: 24.6%;
    -webkit-box-shadow: 0px 0px 0px #00ff0d;
            box-shadow: 0px 0px 0px #00ff0d;
}

.Card.Card-Glow {
  -webkit-animation: glow .5s ease-in-out 0s 1 alternate;
          animation: glow .5s ease-in-out 0s 1 alternate;
  -webkit-transform: scale(1, 1);
      -ms-transform: scale(1, 1);
          transform: scale(1, 1);
}

@-webkit-keyframes glow {
  0% { -webkit-box-shadow: 0px 0px 0px #00ff0d; box-shadow: 0px 0px 0px #00ff0d; }
  50% { -webkit-box-shadow: 0px 0px 25px #00ff0d; box-shadow: 0px 0px 25px #00ff0d; }
  100% { -webkit-box-shadow: 0px 0px 0px #00ff0d; box-shadow: 0px 0px 0px #00ff0d; }
}

@keyframes glow {
  0% { -webkit-box-shadow: 0px 0px 0px #00ff0d; box-shadow: 0px 0px 0px #00ff0d; }
  50% { -webkit-box-shadow: 0px 0px 25px #00ff0d; box-shadow: 0px 0px 25px #00ff0d; }
  100% { -webkit-box-shadow: 0px 0px 0px #00ff0d; box-shadow: 0px 0px 0px #00ff0d; }
}


